body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
}

.hero {
  height: 100vh;
  background: url("https://images.unsplash.com/photo-1503376780353-7e6692767b70")
    center/cover no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.overlay {
  background: rgba(0, 0, 0, 0.75);
  padding: 60px;
  text-align: center;
  color: white;
  border-radius: 12px;
  animation: fadeIn 1.5s ease-in-out;
}

.overlay h1 {
  font-size: 70px;
  letter-spacing: 4px;
  margin: 0;
}

.overlay p {
  font-size: 20px;
  margin: 20px 0;
  opacity: 0.8;
}

button {
  padding: 12px 30px;
  background: #ff3c00;
  color: white;
  border: none;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s;
}

button:hover {
  background: #ff6a00;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
